<template>
  <div>
    <v-container class="nosotros__container">
      <title-ribbon :title="'Nosotros'" />
      <div class="nosotros_container__main">
        <v-row class="nosotros_row__img">
          <v-col>
            <v-img
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/centro-de-salud-mental"
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="nosotros_row__somos">
          <v-col>
            <p class="p_first">
              <strong>Somos más de 300 personas, </strong>
              con vocación de servicio, coordinadas en una labor común,
              altanmente gratificante que se traduce en facilitar el acceso a
              una mejor calidad de vida a muchas personas: las que solicitan
              nuestros servicios y quienes constituyen su entorno inmediato. El
              contingente central está constituído por un equipo de
              profesionales del área de la salud mental, emocional y conductual:
              psicólogos, psiquiatras, neurólogos, psicopedagogos,
              fonoaudiólogos, nutricionistas y terapeutas ocupacionales que
              creen que cada persona puede superar sus problemas cotidianos y
              existenciales, mejorando significativamente su calidad de vida. Se
              requiere para ello una ayuda especializada, accesible, no
              excluyente, que propicie y haga efectivo un esfuerzo compartido.
            </p>
            <p class="p_last">
              El centro es una realidad consolidada en esta área. En más de 40
              años hemos escuchado, entregándoles herramientas para solucionar
              sus variados problemas, a miles de personas que buscaron y
              encontraron nuestra ayuda, tanto en Santiago como en Viña del Mar,
              e incluso a través de la telemedicina, llegando a personas a lo
              largo de todo Chile e incluso otras partes del mundo.
            </p>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="nosotros_row__grupo">
          <v-col class="prm_col1">
            <p class="p_first">
              <strong>Nuestro especializado grupo de profesionales, </strong>
              apoyado en un eficiente equipo ejecutivo y administrativo, es la
              garantía que la psicología, psiquiatría, neurología,
              fonoaudiología, terapia ocupacional, nutricion y psicopedagogía
              sean disciplinas más cercanas, más amables y al alcance de todos
            </p>
            <p>
              Desde su nacimiento, en la psicología y la psiquiatría se ha
              manifestado una tendencia a ser áreas de atención para una minoría
              debido a sus costos y a una relación que ha ido creando
              estereotipos, mitos y leyendas.
            </p>
            <p class="p_last">
              Nosotros creemos que todos los seres humanos necesitamos unos de
              otros y la terapia es una instancia especializada y profesional
              para construir las bases de un crecimiento personal genuino a
              través de revisión, análisis, comprensión, aprendizaje, solución
              de problemas, toma de decisiones, crecimiento y desarrollo.
            </p>
          </v-col>
        </v-row>
        <splitter />
        <message-ribbon
          v-bind="{
            msg: `
                Por lo tanto, puedes visitarnos y recibir ayuda de un especialista del área, <br>
                y pondremos todo nuestro esfuerzo y conocimiento a tu servicio
            `,
          }"
        />
        <splitter />
        <v-row class="nosotros_row__profesionales">
          <v-col class="col-4 nrp-select_sucursal">
            <label>
              Revisa los profesionales acá:
            </label>
            <v-select
              v-model="sucursalToLink"
              :items="sucursales"
              filled
              label="Selecciona Sucursal"
              item-value="ToLink"
              item-text="Nombre"
            />
          </v-col>
          <v-col class="col-8 nrp_botonera">
            <div
              v-if="buttonsCharged"
              class="nrp-botonera_main"
            >
              <div class="nrp-botonera_title">
                <h4>Ver Profesionales {{ sucursal.Nombre }}</h4>
              </div>
              <div class="nrp-botonera_btn">
                <v-btn
                  v-for="(especialidad, i) in sucursal.Especialidades"
                  :key="i"
                  :to="`/terapeutas/${sucursal.ToLink}/${especialidad.ToLink}`"
                >
                  {{ especialidad.NombreCompleto }}
                </v-btn>
              </div>
            </div>
          </v-col>
        </v-row>
        <splitter-volver />
      </div>
    </v-container>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  import Vue from 'vue'
  export default {
    name: 'Nosotros',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      MessageRibbon: () => import('@/components/web/base/MessageRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    data () {
      return {
        sucursal: false,
        sucursalToLink: false,
        buttonsCharged: false,
      }
    },
    computed: {
      ...mapState('web', ['sucursales']),
    },
    watch: {
      sucursalToLink: function () {
        this.buttonsCharged = false
        this.getSucursal(this.sucursalToLink)
      },
    },
    methods: {
      getSucursal (toLink) {
        const body = {
          addEspecialidades: true,
        }
        Vue.axios
          .post(`/api/sucursales/${toLink}`, body)
          .then(response => {
            this.sucursal = response.data.result[0]
            this.buttonsCharged = true
          })
          .catch(err => {
            console.log(err)
          })
      },
    },
    metaInfo: {
      title: 'Nosotros',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
